.ExteriorContainer{
    padding: 5vh 2.5vw;
    background: var(--light);
    margin-top: 2rem;
    margin-bottom: 10rem;
    padding-left: 5vw;
}

.Left{
    margin-left: -5vw;
}

.Right{
    margin-right: -5vw;
}

.ExteriorContainer > h2 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
}

.MainAbout > h1 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
}

.ContentContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.ContentContainer > img {
    width: 20vw;
    height: auto;
    margin: 0 2rem;
}

.MainAbout > .ContentContainer > img {
    width: 25vw;
}

.SummaryContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.Left > .ContentContainer > .SummaryContainer {
    -ms-flex-align: end;
        align-items: flex-end;
}

.Right > .ContentContainer >  .SummaryContainer {
    -ms-flex-align: start;
        align-items: flex-start;
}

.Left > .ContentContainer > .SummaryContainer > p{
    -ms-flex-item-align: start;
        align-self: flex-start;
}

.MainWorkExteriorContainer{
    padding: 5vh 2.5vw;
    background: var(--pDark);
    margin-top: 2rem;
    margin-bottom: 10rem;
    margin-right: -5vw;
    padding-left: 5vw;
    color: var(--light);
}

.MainWorkExteriorContainer > h3 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.MainWorkContentContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.MainWorkContentContainer > img {
    width: 20vw;
    height: auto;
    margin: 0 2rem;
}

.MainWorkSummaryContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
}



